export default class TtlService {

  constructor(resource){
    this._resource = resource('z1/ttls{/id}');
  }

  lista() {
      return this._resource
          .query()
          .then(res => res.json(), err => {
              console.log(err);
              throw new Error('Não foi possível obter as ttl');
          });
  }

  cadastra(ttl) {

      if(ttl._id) {

          return this._resource
              .update({ id: ttl._id}, ttl);

      } else {
          return this._resource
              .save(ttl);
      }

  }

  apaga(id) {

      return this._resource
          .delete({ id })
          .then(null, err => {
              console.log(err);
              throw new Error('Não foi possível remover a ttl');
          })
  }

  busca(id) {

      return this._resource
          .get({ id })
          .then(res => res.json());
  }
}



