<template lang="html">
  <div class="cadastro">
    <h1>Zona</h1>
    <hr>
    <h2 v-if="zona._id">Alterar Zona</h2>
    <h2 v-else>Nova Zona</h2>
    <hr>
    <form role="Form" @submit.prevent="grava()">
      <table class="table">
        <thead>
          <tr>
            <th scope="col">Nome</th>
            <th scope="col">TTL</th>
            <th scope="col">Classes</th>
            <th scope="col">Tipo</th>
            <th v-if="zona.tipo == 'MX'" scope="col">Prioridade</th>
            <th scope="col">Registro</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th><input type="text" class="form-control" v-model='zona.host' placeholder="www"></th>
            <th><input type="text" class="form-control" v-model='zona.ttl' placeholder="14400"></th>
            <th><input type="text" class="form-control" v-model='zona.classe' placeholder="IN"></th>
            <th>
              <select v-model="zona.tipo" class="form-control " id="sel1">
                <option v-for='tipo of tipos'>{{tipo}}</option>
              </select>
            </th>
            <th v-if="zona.tipo == 'MX'"><input  type="text" class="form-control" v-model='zona.mx' value="0"></th>
            <th><input type="text" class="form-control" v-model='zona.registro'placeholder="1.1.1.1"></th>
          </tr>
        </tbody>
      </table>
      <div class="pull-right">
        <div class="btn-group ">
          <router-link :to="{ name : 'home'}">
            <button type="button" class="btn btn-danger">Cancelar</button>
          </router-link>
        </div>
        <div class="btn-group ">
          <button type="submit" class="btn btn-success">Salva</button>
        </div>
      </div>
    </form>

  </div>
</template>

<script>
import Zona from '../../domain/zona/Zona';
import ZonaService from '../../domain/zona/ZonaService';
export default {
  data(){
    return {
      zona: new Zona(),
      tipos:['A','CNAME','TXT','MX'],
      id: this.$route.params.id

    }
  },
  methods:{
    grava(){
      this.service
        .cadastra(this.zona)
        .then(() => {
          if(this.id) this.$router.push({ name: 'home' });
          this.zona = new Zona();
        }, err => {
          this.$router.push("login");
          console.log(err)
        });

    }
  },
  created() {

    this.service = new ZonaService(this.$resource);

    if(this.id){
      this.service
        .busca(this.id)
        .then(zona => this.zona = zona, err => {
          this.$router.push("login");
          console.log(err)
        });
    }
  }
}
</script>

<style lang="css">
</style>



