const store = {
  _username: '',
  // _token: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJsb2dpbiI6ImFkbSIsImlhdCI6MTU0Njg3ODIzOCwiZXhwIjoxNTQ2OTYyODM4fQ.3469nINDZ2Uc7HAvhV3pmqXYQrSfVsbu0o922CB-0eE',
  _token: '',

  isLogged () {
    return this.token
  },

  set username (str) {
    this._username = str
    localStorage.setItem('username',str)
  },
  get username () {
    return this._username || localStorage.getItem('username')
  },

  set token (str) {
    this._token = str
    localStorage.setItem('token',str)
  },
  get token () {
    return this._token || localStorage.getItem('token')
  }

}
export default store;



