export default class Ttl {

  constructor(ttl="",dominio="",dominiottl="",classe="IN",tipo="SOA",ns1="",ns2="",serial="",refresh="",retry="",expire="",minttl=""){
    this.ttl=ttl;
    this.dominio=dominio;
    this.classe=classe;
    this.tipo=tipo
    this.ns1=ns1;
    this.ns2=ns2;
    this.serial=serial;
    this.refresh=refresh;
    this.retry=retry;
    this.expire=expire;
    this.minttl=minttl;
    this.dominiottl=dominiottl;
  }
}



