<template lang="html">
<div class="home">
  <h1>Zonas</h1>
    <div class="form-inline">
      <input type="search" name="zonas" class="form-control" @input="filtro = $event.target.value" placeholder="Filtre por nomes">
      <router-link :to="{ name : 'cadastro', params: {} }">
        <button type="button" class="btn btn-primary mb-2">Adicionar Registro</button>
      </router-link>
      <button type="button" @click="logout()" class="btn btn-danger pull-right space">
          <span class="glyphicon glyphicon-log-out"></span>
      </button>
      <span v-for="(ttl, index) of ttls">
        <router-link :to="{ name : 'ttl', params: {id: ttl._id} }" v-if="index == 0">
          <button type="button" class="btn btn-warning pull-right space">
              <span class="glyphicon glyphicon-wrench"></span>
          </button>
        </router-link>
      </span>
      <router-link :to="{ name : 'user', params: {} }">
        <button type="button" class="btn btn-primary mb-2 pull-right">
          <span class="glyphicon glyphicon-user"></span>
        </button>
      </router-link>
    </div>
  <hr>
    <meu-painel :zonas='zonasComFiltro' @removerZona="apagarZona($event)"></meu-painel>
</div>
</template>

<script>
import Painel from '../shared/painel/Painel.vue';
import ZonaService from '../../domain/zona/ZonaService';
import TtlService from '../../domain/ttl/TtlService';
import store from '../../store/store'
export default {
  components: {
    'meu-painel' : Painel
  },
  data(){
    return {
        zonas: [],
        ttls: [],
        filtro: '',
        store
    }
  },
  computed: {
    zonasComFiltro(){
      if(this.zonas){
        let exp = new RegExp(this.filtro.trim(), 'i');
        return this.zonas.filter(zona => exp.test(zona.host));
      }else{
        return this.zonas;
      }
    }
  },
  methods: {
    apagarZona(zonainfo){
      this.zonaService.apaga(zonainfo.zona._id)
        .then(()=> {
          let indice = zonainfo.indice;
          this.zonas.splice(indice, 1);
          this.mensagem = 'Zona removida com sucesso';
        }, err => {
          this.mensagem = err.message;
        });
    },
    logout(){
      this.store.token = '';
      this.store.username = '';
      this.$router.push('login');
    }
  },
  created() {

    this.zonaService = new ZonaService(this.$resource);
    this.ttlService = new TtlService(this.$resource);

    this.zonaService
      .lista()
      .then(zonas => this.zonas = zonas, err => {
        this.$router.push("login");
        this.mensagem = err.message
      });

    this.ttlService
      .lista()
      .then(ttls => this.ttls = ttls, err => {
        this.$router.push("login");
        this.mensagem = err.message
      });


  }
}
</script>

<style lang="css" scoped>
 h1{
   text-align: center;
 }
 .space{
   margin: 0 5px;
 }
</style>



