<template lang="html">
  <div class="col">
    <h1>Usuarios</h1>
    <hr>
    <h2>Novo Usuario</h2>
    <hr>
    <form role="Form" @submit.prevent="grava()">
      <div class="form-group">
        <label>Login</label>
        <input type="text" class="form-control" v-model="user.login">
      </div>
      <div class="form-group">
        <label >Password</label>
        <input type="password" class="form-control" v-model="passwd.senha">
      </div>
      <div class="form-group">
        <label>Confirmacao</label>
        <input type="password" class="form-control" v-model="passwd.confirmacao">
      </div>
      <button type="submit" class="btn btn-default">Submit</button>
    </form>
    <hr>
    <h2>Usarios Cadastrados</h2>
    <table class="table table-striped">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">User</th>
          <th colspan="3"></th>
          <th scope="col" colspan="1">Ações</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for='(user,index) of users'>
          <td scope="row">{{index}}</td>
          <td>{{user.login}}</td>
          <td class="showMe" colspan="3"></td>
          <td class="noShowMe form-group" colspan='1'>
            <label>Senha</label>
            <input type="password" class="form-control" v-model="passwd.senha" name="" value="">
          </td>
          <td class="noShowMe form-group" colspan='1'>
            <label>Senha (novamente)</label>
            <input type="password" class="form-control" v-model="passwd.confirmacao" name="" value="">
          </td>
          <td class="noShowMe form-group">
            <button type="button" class="btn btn-link" @click='save(user, index)'>
                <span class="glyphicon glyphicon-floppy-save"></span>
                Salvar
            </button>
          </td>
          <td>
              <button type="button" class="btn btn-link"  @click='change()'>
                  <span class="glyphicon glyphicon-pencil"></span>
                  Editar
              </button>
            <button type="button" class="btn btn-link" @click='remover(user, index)'>
                <span class="glyphicon glyphicon-trash"></span>
                Excluir
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="pull-right">
      <div class="btn-group ">
        <router-link :to="{ name : 'home'}">
          <button type="button" class="btn btn-danger">Voltar</button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import User from '../../domain/user/User';
import UserService from '../../domain/user/UserService';
export default {
  data(){
    return {
      passwd:{},
      users: [],
      user: new User(),
    }
  },
  methods: {
    remover(user, index) {
      this.service.apaga(user._id)
        .then(()=> {
          this.users.splice(index, 1);
        }, err => {
          this.mensagem = err.message;
        });
    },
    change(){
      this.passwd = {};
      let myrow = event.target.closest('tr');
      let showMe = myrow.querySelector('.showMe');
      let myclass = myrow.querySelectorAll('.noShowMe');
      myrow.removeChild(showMe);
      for (var i = 0; i < myclass.length; i++) {
        myclass[i].classList.toggle('noShowMe');
      }
    },
    validPasswd(){
      if (this.passwd.senha !=null && this.passwd.confirmacao!= null) {
        if(this.passwd.senha === this.passwd.confirmacao){
          for (var i = 0; i < this.users.length; i++) {
            if(this.users[i].login === this.user.login){
              alert("Login já cadastrado");
              return false;
            }
          }
          return true
        }else{
          alert("Senhas são diferentes");
          return false;
        }
      }
      alert("Campo de senhas estão em branco");
      return false;
    },
    save(user){
      if(this.validPasswd()){
        user.senha = this.passwd.senha;
        this.service
          .cadastra(user)
          .then(() => {
            this.user = new User();
            this.passwd = {};
          }, err => {
            this.$router.push("login");
            console.log(err)
          });
      }
    },
    grava(){
      if(this.validPasswd()){
        this.user.senha = this.passwd.senha;
        this.service
          .cadastra(this.user)
          .then(() => {
            this.users.push(this.user);
            this.user = new User();
            this.passwd = {};
          }, err => {
            this.$router.push("login");
            console.log(err)
          });
      }
    }
  },
  created(){
    this.service = new UserService(this.$resource);

    this.service
      .lista()
      .then(users => this.users = users, erro =>{
        this.$router.push("login");
      });
  }
}
</script>

<style lang="css" scoped>
.noShowMe{
  display: none;
}

label{
  float: left;
  margin-right: 10px;
}
input{
  width: auto;
}
</style>



