<template lang="html">
  <div class="">
    <h1>Configuração avançada</h1>
    <hr>
    <form role="Form" @submit.prevent="grava()">
      <table class="table borderless">
        <thead>
          <tr>
            <th scope="col">Dominio</th>
            <th scope="col" colspan="3">TTL</th>
            <th scope="col" colspan="3">Registros</th>
          </tr>
        </thead>
        <tbody  class="borderless">
          <tr>
            <td colspan="7">$TLL  {{ ttl.ttl }}</td>
          </tr>
          <tr>
              <td><input type="text" class="form-control" v-model='ttl.dominio'></td>
              <td><input type="text" class="form-control" v-model='ttl.dominiottl'></td>
              <td>{{ttl.classe}}</td>
              <td>{{ttl.tipo}}</td>
              <td><input type="text" class="form-control" v-model='ttl.ns1'></td>
              <td><input type="text" class="form-control" v-model='ttl.ns2'></td>
              <td>(</td>
          </tr>
          <tr>
            <td colspan="4"></td>
            <td><input type="text" class="form-control" v-model='ttl.serial' disabled></td>
            <td>Serial Number</td>
          </tr>
          <tr>
            <td colspan="4"></td>
            <td><input type="text" class="form-control" v-model='ttl.refresh'></td>
            <td>Refresh</td>
          </tr>
          <tr>
            <td colspan="4"></td>
            <td><input type="text" class="form-control" v-model='ttl.retry'></td>
            <td>Retry</td>
          </tr>
          <tr>
            <td colspan="4"></td>
            <td><input type="text" class="form-control" v-model='ttl.expire'></td>
            <td>Expire</td>
          </tr>
          <tr>
            <td colspan="4"></td>
            <td><input type="text" class="form-control" v-model='ttl.minttl'></td>
            <td>Minimum TTL</td>
            <td colspan="1">)</td>
          </tr>
        </tbody>
      </table>
      <div class="pull-right">
        <div class="btn-group ">
          <router-link :to="{ name : 'home'}">
            <button type="button" class="btn btn-danger">Cancelar</button>
          </router-link>
        </div>
        <div class="btn-group ">
          <button type="submit" class="btn btn-success">Salva</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Ttl from '../../domain/ttl/Ttl';
import TtlService from '../../domain/ttl/TtlService';
export default {
  data(){
    return {
        ttl: new Ttl(),
        id: this.$route.params.id,
    }
  },
  methods: {
    grava() {
      this.service
        .cadastra(this.ttl)
        .then(() => {
          if(this.id) this.$router.push({ name: 'home' });
          this.ttl = new Ttl();
        }, err => {
          this.$router.push("login");
          console.log(err)
        });
    }
  },
  created(){
    this.service = new TtlService(this.$resource);

    if(this.id){
      this.service
        .busca(this.id)
        .then(ttl => this.ttl = ttl, err => {
          this.$router.push("login");
          console.log(err)
        });
    }
  }
}
</script>

<style lang="css" scoped>
.borderless td, .borderless th {
    border: none;
}
</style>



