import Home from './components/home/Home.vue';
import Cadastro from './components/cadastro/Cadastro.vue';
import Ttl from './components/ttl/Ttl.vue';
import Login from './components/login/Login.vue';
import User from './components/user/User.vue';

export const routes = [
  { path: '', name: 'home', component: Home, titulo: 'Home', menu: true},
  { path:'/cadastro', name:'cadastro', component: Cadastro, titulo: 'Cadastro', menu:true},
  { path:'/ttl', name:'ttl', component: Ttl, titulo: 'Cadastro TTL', menu:true},
  { path:'/login', name:'login', component: Login, titulo: 'Login', menu:true},
  { path:'/user', name:'user', component: User, titulo: 'User', menu:true},
  { path:'/cadastro/:id', name:'alterar', component: Cadastro, titulo: 'Edição', menu:false},
  { path:'/ttl/:id', name:'alterarttl', component: Ttl, titulo: 'Edição TTL', menu:false},
  { path:'/user/:id', name:'user', component: User, titulo: 'Edição User', menu:false},
  { path: '*', component: Home, menu: false},

];



