export default class ZonaService {

    constructor(resource) {
        this._resource = resource('z1/zonas{/id}');    }

    lista() {
        return this._resource
            .query()
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('Não foi possível obter as zonas');
            });
    }

    cadastra(zona) {

        if(zona._id) {

            return this._resource
                .update({ id: zona._id}, zona);

        } else {
            return this._resource
                .save(zona);
        }

    }

    apaga(id) {

        return this._resource
            .delete({ id })
            .then(null, err => {
                console.log(err);
                throw new Error('Não foi possível remover a zona');
            })
    }

    busca(id) {

        return this._resource
            .get({ id })
            .then(res => res.json());
    }

}



