<template lang="html">
  <div class="col">
    <table class="table table-striped">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Nome</th>
          <th scope="col">TTL</th>
          <th scope="col">Classes</th>
          <th scope="col">Tipo</th>
          <th scope="col">Registro</th>
          <th scope="col" colspan="2">Ações</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(zona, index) of zonas">
          <th scope="row">{{ index }}</th>
          <td>{{ zona.host }}</td>
          <td>{{ zona.ttl }}</td>
          <td>{{ zona.classe }}</td>
          <td>{{ zona.tipo }} {{ zona.mx }}</td>
          <td>{{ zona.registro }}</td>
          <td>
            <router-link :to="{ name : 'alterar', params: { id: zona._id} }">
              <button type="button" class="btn btn-link">
                  <span class="glyphicon glyphicon-pencil"></span>
                  Editar
              </button>
            </router-link>
            <button type="button" class="btn btn-link" @click='remover(zona)'>
                <span class="glyphicon glyphicon-trash"></span>
                Excluir
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  props: ['zonas'],
  data(){
    return{
    }
  },
  methods: {
    remover(zona) {
      if(confirm("Você realmente quer excluir esse registro ?")){
        let indice = this.zonas.indexOf(zona);
        this.$emit('removerZona', {zona, indice});
      }
      return;
    }
  }
}
</script>
<style lang="css" scoped>
</style>



