export default class TtlService {

  constructor(resource){
    this._resource = resource('z1/user{/id}');
  }
  cadastra(user) {

      if(user._id) {

          return this._resource
              .update({ id: user._id}, user);

      } else {
          return this._resource
              .save(user);
      }

  }
  lista() {
      return this._resource
          .query()
          .then(res => res.json(), err => {
              console.log(err);
              throw new Error('Não foi possível obter as user');
          });
  }
  apaga(id) {

      return this._resource
          .delete({ id })
          .then(null, err => {
              console.log(err);
              throw new Error('Não foi possível remover o usuario');
          })
  }
}



